.advantages {
  @include section;
  overflow: hidden;
  &__content {
    margin-bottom: 24px;
  }
  &__slider {
    overflow: visible;
    &-wrapper {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: $gap;
      @media (max-width: 1279px) {
        grid-template-columns: repeat(2, 1fr);
      }
      @media (max-width: 767px) {
        display: flex;
        gap: 0;
        margin-bottom: 16px;
      }
    }
  }
  &__item {
    width: 100%;
    height: auto;
    min-height: 225px;
    padding: 39px 19px 40px;
    background-color: $white;
    border: 1px solid $grey-light; 
    border-radius: 16px;
    @include shadow-grey;
    text-align: center;
    &:is(a) {
      display: flex;
      justify-content: center;
      align-items: center;
      border: none;
      background-color: $green;
      @include shadow-green;
      transition: all $transition;
      @media (min-width: 1280px) {
        &:hover {
          background-color: $green-dark;
          box-shadow: none;
        }
      }
      @media (max-width: 767px) {
        &.mobile {
          display: flex !important;
          min-height: auto;
          padding: 30px 52px;
          margin-bottom: 16px;
        }
      }
      .advantages__item-title {
        @include button-rimma;
        text-transform: uppercase;
        margin: 0;
      }
    }
    &-img {
      width: 24px;
      height: 24px;
      margin: 0 auto 9px;
      img {
        @include contain;
      }
    }
    &-title {
      @include h3-bold;
      font-variant-numeric: lining-nums;
      margin-bottom: 11px;
    }
    &-description {
      @include body-medium;
    }
  }
  &__bottom {
    display: flex;
    justify-content: center;
  }
  &__small {
    display: flex;
    align-items: center;
    gap: 11px;
    @media (max-width: 767px) {
      text-align: center;
      margin: 0 auto;
      max-width: 282px;
    }
    &-img {
      width: 24px;
      height: 24px;
      flex-shrink: 0;
      img {
        @include contain;
      }
    }
    &-title {
      @include body-medium;
      color: $grey-dark;
    }
  }
}