.slider {
  &-kit {
    display: none;
    justify-content: center;
    align-items: center;
    gap: 12px;
    @media (max-width: 767px) {
      display: flex;
    }
    .slider {
      &-button {
        &-prev, &-next {
          position: static;
          transform: none;
        }
      }
    }
  }
  &-button {
    &-prev, &-next {
      display: none;
      justify-content: center;
      align-items: center;
      width: 32px;
      height: 32px;
      flex-shrink: 0;
      background-color: $white;
      border: 1px solid $grey-light;
      border-radius: 50%;
      transform: translateY(-50%);
      outline: none;
      margin: 0;
      z-index: 10;
      @include shadow-grey;
      transition: all $transition;
      @media (max-width: 767px) {
        display: flex;
      }
      &.swiper-button-disabled {
        opacity: .3;
      }
      &.swiper-button-lock {
        display: none;
      }
      &::after {
        content: none;
      }
      svg {
        width: 24px;
        height: 24px;
        stroke: $black;
        stroke-width: 2;
        transition: all $transition;
      }
    }
    &-prev {
      left: 0;
    }
    &-next {
      right: 0;
    }
  }
}