.popup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba($black, .25);
  z-index: 510;
  overflow-y: auto;
  overflow-x: hidden;
  &_active {
    display: block;
  }
  &__wrap {
    min-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 24px;
    @media (max-width: 1279px) {
      padding: 16px;
    }
  }
  &__content {
    max-width: 380px;
    width: 100%;
    padding: 24px;
    background-color: $white;
    border-radius: 16px;
    position: relative;
    z-index: 1;
    @media (max-width: 767px) {
      padding: 16px;
    }
  }
  &__heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 32px;
    .logo {
      display: inline-flex;
      width: 110px;
      height: 39px;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: 100%;
      }
    }
  }
  &__close {
    display: flex;
    width: 40px;
    height: 40px;
    cursor: pointer;
    z-index: 1;
    span {
      width: 20px;
    }
  }
  &__text {
    text-align: center;
    margin-bottom: 32px;
  }
  &__title {
    @include h3-bold;
    letter-spacing: normal;
    &:not(:last-child) {
      margin-bottom: 8px;
    }
  }
  &__description {
    @include body-medium;
    color: $grey-dark;
    letter-spacing: normal;
  }
  &__button {
    width: 100%;
  }
}