.header {
  position: fixed;
  top: 20px;
  left: 0;
  right: 0;
  z-index: 500;
  transition: all $transition, padding-right 0s;
  @media (max-width: 767px) {
    top: 16px;
  }
  .logo {
    display: inline-flex;
    width: 131px;
    height: 47px;
    flex-shrink: 0;
    svg {
      width: 100%;
      height: 100%;
    }
  }
  &__body {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: $gap;
    padding: 11px;
    background-color: $white;
    border-radius: 16px;
    border: 1px solid $grey-light;
    @include shadow-grey;
  }
  &__content {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: $gap;
    .nav {
      margin: 0 auto;
      @media (max-width: 1279px) {
        margin: 0;
      }
    }
    @media (max-width: 1279px) {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      height: 100vh;
      height: 100dvh;
      margin: 0;
      z-index: -2;
      transform: translateY(-150%);
      transition: all .4s ease;
      padding: 127px 16px 0;
      background: $white;
      flex-direction: column;
      justify-content: initial;
      align-items: flex-start;
      gap: 32px;
      overflow-x: hidden;
      overflow-y: auto;
      &_active {
        transform: translateY(0%);
      }
    }
  }
  &__button {
    @media (max-width: 1279px) {
      flex-shrink: 0;
    }
  }
  .social {
    display: none;
    @media (max-width: 1279px) {
      display: inline-flex;
    }
  }
  .connection {
    display: none;
    @media (max-width: 1279px) {
      display: block;
      width: calc(100% + 32px);
      margin: auto -16px 0;
    }
  }
}