@mixin cover {
  height: 100%;
  object-fit: cover;
}

@mixin contain {
  height: 100%;
  object-fit: contain;
}

@mixin rimmasans {
  font-family: 'Rimma Sans', sans-serif;
}

@mixin section {
  padding: 140px 0;
  @media (max-width: 1279px) {
    padding: 80px 0;
  }
  @media (max-width: 767px) {
    padding: 60px 0;
  }
}

@mixin h1 {
  @include rimmasans;
  font-size: 50px;
  font-weight: 700;
  @media (max-width: 1279px) {
    font-size: 32px;
  }
  @media (max-width: 767px) {
    font-size: 24px;
  }
}

@mixin h2-medium {
  font-size: 24px;
  font-weight: 500;
  // line-height: 1.21;
  @media (max-width: 1279px) {
    font-size: 19px;
  }
  @media (max-width: 767px) {
    font-size: 17px;
  }
}

@mixin h2-bold {
  font-size: 24px;
  font-weight: 700;
  // line-height: 1.21;
  letter-spacing: -0.02em;
  @media (max-width: 1279px) {
    font-size: 19px;
  }
  @media (max-width: 767px) {
    font-size: 17px;
  }
}

@mixin h3-medium {
  font-size: 19px;
  font-weight: 500;
  // line-height: 1.21;
  letter-spacing: -0.02em;
  @media (max-width: 1279px) {
    font-size: 17px;
  }
}

@mixin h3-bold {
  font-size: 19px;
  font-weight: 700;
  // line-height: 1.21;
  letter-spacing: -0.02em;
  @media (max-width: 1279px) {
    font-size: 17px;
  }
}

@mixin body-medium {
  font-size: 16px;
  font-weight: 500;
  // line-height: 1.19;
  letter-spacing: -.02em;
}

@mixin body-bold {
  font-size: 16px;
  font-weight: 600;
  // line-height: 1.19;
}

@mixin button {
  font-size: 16px;
  font-weight: 600;
  line-height: 1.1;
}

@mixin button-rimma {
  @include rimmasans;
  font-size: 27px;
  font-weight: 700;
  line-height: 1;
  letter-spacing: -0.02em;
  @media (max-width: 767px) {
    font-size: 20px;
  }
}

@mixin label {
  font-size: 13px;
  font-weight: 600;
  // line-height: 1.08;
  line-height: 1.1;
}

@mixin shadow-grey {
  box-shadow: 0 4px 12px rgba(#DEDEDE, .35);
}

@mixin shadow-green {
  box-shadow: 0 4px 12px rgba(#8EC900, .3);
}

@mixin shadow-dark {
  box-shadow: 8px 10px 21px rgba(#474747, .21);
}