.hero {
  overflow: hidden;
  position: relative;
  z-index: 1;
  &::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 179px;
    background: linear-gradient(180deg, rgba($bg, 0) 0%, rgba($bg, 1) 100%), linear-gradient(180deg, rgba($bg, 0) 0%, rgba($bg, .5) 100%);
    z-index: 2;
    pointer-events: none;
    @media (max-width: 1279px) {
      height: 99px;
    }
  }
  &__content {
    // min-height: 977px;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    padding: 100px 0 7px;
    position: relative;
    z-index: 1;
    @media (max-width: 1279px) {
      min-height: 650px;
      padding-bottom: 5px;
    }
    @media (max-width: 767px) {
      min-height: auto;
      padding: 87px 0 23px;
    }
  }
  &__text {
    max-width: 761px;
    margin: auto 0;
    padding: 40px 0 14px 111px;
    @media (max-width: 1279px) {
      max-width: 400px;
      padding: 40px 0 10px;
    }
    @media (max-width: 767px) {
      margin: 0 auto;
      max-width: 500px;
      padding: 32px 0 415px;
    }
  }
  &__title {
    @include h1;
    margin-bottom: 24px;
    @media (max-width: 1279px) {
      margin-bottom: 16px;
    }
    @media (max-width: 767px) {
      font-size: 27px;
    }
    @media (max-width: 374px) {
      font-size: 24px;
    }
    span {
      color: $green;
    }
  }
  &__subtitle {
    @include h2-medium;
    margin-bottom: 36px;
    @media (max-width: 1279px) {
      margin-bottom: 24px;
    }
    @media (max-width: 767px) {
      margin-bottom: 40px;
    }
  }
  &__button {
    width: 318px;
    height: 58px;
    @media (max-width: 1279px) {
      width: 360px;
    }
  }
  &__img {
    position: absolute;
    top: 140px;
    bottom: 0;
    left: 712px;
    width: 654px;
    height: auto;
    z-index: 2;
    pointer-events: none;
    @media (max-width: 1279px) {
      top: 100px;
      left: 49.5vw;
      width: 425px;
    }
    @media (max-width: 767px) {
      top: auto;
      left: 65%;
      transform: translateX(-50%);
    }
    img {
      @include contain;
      object-position: bottom;
    }
  }
}