.heading {
  text-align: center;
  margin-bottom: 39px;
  @media (max-width: 1279px) {
    margin-bottom: 29px;
  }
  @media (max-width: 767px) {
    margin-bottom: 35px;
  }
  &__suptitle {
    display: inline-flex;
    align-items: center;
    gap: 8px;
    @include body-bold;
    color: $grey-dark;
    margin-bottom: 13px;
    position: relative;
    @media (max-width: 1279px) {
      margin-bottom: 12px;
    }
    @media (max-width: 767px) {
      margin-bottom: 13px;
    }
    &::before {
      content: '';
      width: 16px;
      height: 16px;
      background-color: $green;
    }
  }
  &__title {
    @include h1;
    &-img {
      display: inline-block;
      vertical-align: middle;
      width: 75px;
      height: 75px;
      margin: -3px -6px -5px -6px;
      @media (max-width: 1279px) {
        width: 36px;
        height: 36px;
        margin: -5px -7px 0px -5px;
      }
      @media (max-width: 767px) {
        margin: -5px 0 3px 0;
      }
      svg {
        width: 100%;
        height: 100%;
      }
    }
  }
}