.steps {
  @include section;
  background-color: $white;
  &__heading {
    margin-bottom: 147px;
    @media (max-width: 767px) {
      margin-bottom: 29px;
    }
  }
  &__decor {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: $gap;
    margin-bottom: 17px;
    @media (max-width: 1279px) {
      display: none;
    }
    &-item {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: center;
      position: relative;
    }
    &-line {
      position: absolute;
      left: 51%;
      bottom: 26px;
      width: calc(100% + 8px);
      height: 79px;
      svg {
        width: 100%;
        height: 100%;
      }
    }
    &-dot {
      width: 16px;
      height: 16px;
      flex-shrink: 0;
      border-radius: 50%;
      background-color: $black;
      opacity: 0;
    }
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &__item {
    padding: 22px 21px 58px;
    background-color: $white;
    border: 1px solid $grey-light; 
    border-radius: 16px;
    @include shadow-grey;
    opacity: 0;
    @media (max-width: 1279px) {
      padding: 39px 19px;
      opacity: 1;
    }
    @media (max-width: 767px) {
      padding: 15px 16px;
    }
    &-number {
      padding-left: 3px;
      @include button-rimma;
      color: $green;
      margin-bottom: 39px;
      @media (max-width: 1279px) {
        padding-left: 0;
        margin-bottom: 16px;
      }
    }
    &-description {
      @include body-medium;
      font-variant-numeric: lining-nums;
    }
  }
}