.about {
  @include section;
  background: linear-gradient(180deg, rgba($bg, 1) 0%, rgba($white, 1) 100%);
  overflow: hidden;
  &__slider {
    overflow: visible;
    &-wrapper {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: $gap;
      @media (max-width: 1279px) {
        grid-template-columns: repeat(2, 1fr);
      }
      @media (max-width: 767px) {
        display: flex;
        gap: 0;
        margin-bottom: 16px;
      }
    }
  }
  &__item {
    width: 100%;
    height: auto;
    padding: 29px 29px;
    background-color: $white;
    border: 1px solid $grey-light; 
    border-radius: 16px;
    @include shadow-grey;
    text-align: center;
    @media (max-width: 1279px) {
      min-height: 265px;
      padding-bottom: 16px;
    }
    @media (max-width: 767px) {
      min-height: auto;
      padding-bottom: 38px;
    }
    &-img {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 60px;
      height: 60px;
      background-color: $green-light;
      border-radius: 8px;
      margin: 0 auto 31px;
      svg {
        width: 32px;
        height: 32px;
        fill: $green-dark;
      }
    }
    &-title {
      @include h2-bold;
      margin-bottom: 8px;
    }
    &-description {
      @include body-medium;
    }
  }
}