.feedback {
  padding: 40px 0 140px;
  @media (max-width: 1023px) {
    position: relative;
    padding: 0;
    .container {
      padding: 0;
    }
  }
  &__content {
    padding: 61px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    // background: radial-gradient(103.04% 109.98% at 45.68% 59.69%, #F7F7F7 15.97%, #FFF 40.31%, #D5F589 62.47%, #A7E907 82.16%);
    border-radius: 24px;
    @include shadow-grey;
    position: relative;
    z-index: 1;
    overflow: hidden;
    @media (max-width: 1023px) {
      padding: 84px 16px 75px;
      border-radius: 0;
    }
    @media (max-width: 767px) {
      padding: 75px 16px;
    }
  }
  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    img {
      @include cover;
    }
  }
  &__text {
    max-width: 1080px;
    text-align: center;
    @media (max-width: 1023px) {
      max-width: 550px;
    }
  }
  &__title {
    @include h1;
    margin-bottom: 23px;
  }
  &__subtitle {
    @include h3-medium;
    margin-bottom: 36px;
    @media (max-width: 767px) {
      br {
        display: none;
      }
    }
  }
  &__button {
    height: 58px;
    @media (max-width: 1023px) {
      width: 360px;
    }
    @media (max-width: 767px) {
      width: auto;
    }
  }
}