.form {
  &__content {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: $gap;
    margin-bottom: 32px;
  }
  &__button {
    width: 100%;
  }
}

.label {
  display: flex;
  flex-direction: column;
  gap: 4px;
  cursor: pointer;
  position: relative;
  z-index: 1;
  &__title {
    position: absolute;
    top: 50%;
    left: 16px;
    transform: translateY(-50%);
    @include body-medium;
    color: $grey-dark;
    letter-spacing: normal;
    pointer-events: none;
    transition: all $transition;
  }
}

input, textarea {
  width: 100%;
  height: 60px;
  padding: 16px 15px 0px;
  background-color: $bg;
  border: 1px solid $bg;
  border-radius: 10px;
  appearance: none;
  @include body-medium;
  color: $black;
  font-variant-numeric: lining-nums;
  transition: all $transition;
  outline: none;
  cursor: pointer;
  &:hover {
    background-color: $white;
    border-color: $green;
  }
  &:focus, &:active {
    background-color: $white;
    border-color: $green;
    ~ .label__title {
      @include label;
      transform: translateY(-16px);
    }
  }
  &:not(:placeholder-shown) {
    ~ .label__title {
      @include label;
      transform: translateY(-16px);
    }
  }
  &::placeholder {
    color: transparent;
  }
  &.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}

textarea {
  resize: none;
}