.nav {
  &__list {
    display: flex;
    align-items: center;
    gap: 33px;
    @media (max-width: 1279px) {
      flex-direction: column;
      align-items: flex-start;
      gap: 22px;
    }
  }
  &__link {
    display: flex;
    align-items: center;
    gap: 6px;
    @include body-medium;
    transition: all $transition;
    &_active {
      color: $green-dark;
    }
  }
}