*, *::before, *::after {
  box-sizing: border-box;
}

html {
  height: 100%;
  font-family: $font_base;
  font-size: 16px;
  font-style: normal;
  font-weight: normal;
  -webkit-animation: bugfix infinite 1s;
  line-height: 1.2;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-style: normal;
  font-weight: normal;
  -webkit-animation: bugfix infinite 1s;
  line-height: 1.2;
  margin: 0;
  padding: 0;
  color: $black;
  background-color: $bg;
}

main {
  flex-grow: 1;
}

footer {
  flex-shrink: 0;
}

input, textarea {
  -webkit-animation: bugfix infinite 1s;
  line-height: inherit;
  margin: 0;
  padding: 0;
}

a {
  color: unset;
}

a, a:hover {
  text-decoration: none
}

button, input, a, textarea {
  outline: none;
  cursor: pointer;
  font: inherit;
  &:focus, &:active {
    outline: none
  }
}

button {
  display: flex;
  padding: 0;
  background: none;
  border: none;
  text-align: left;
  color: inherit;
}

h1, h2, h3, h4, h5, h6 {
  font: inherit;
  margin: 0;
  padding: 0;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  width: 100%;
  height: auto;
  display: block;
  // image-rendering: -webkit-optimize-contrast;
}

ul, ol {
  padding: 0;
  margin: 0;
  list-style-type: none;
  li {
    margin: 0;
    padding: 0;
  }
}

.container {
  max-width: 1440px;
  padding: 0 60px;
  margin: 0 auto;
  @media (max-width: 1365px) {
    padding: 0 24px;
  }
  @media (max-width: 1279px) {
    padding: 0 16px;
  }
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

:focus {
  outline-style: none;
  outline-width: 0 !important;
  outline-color: none !important;
}

.desktop {
  @media (max-width: 767px) {
    display: none !important;
  }
}

.mobile {
  display: none !important;
  @media (max-width: 767px) {
    display: block !important;
  }
}

.o-hidden {
  overflow: hidden;
}

.d-none {
  display: none !important;
}