.partners {
  @include section;
  &__heading {
    margin-bottom: 59px;
    @media (max-width: 1279px) {
      margin-bottom: 57px;
    }
    @media (max-width: 767px) {
      margin-bottom: 17px;
    }
  }
  &__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 80px $gap;
    @media (max-width: 1279px) {
      row-gap: 48px;
    }
    @media (max-width: 767px) {
      row-gap: 32px;
    }
  }
  &__item {
    flex: 0 0 calc(25% - 12px);
    width: 100%;
    height: 110px;
    position: relative;
    &:nth-child(4n):not(:last-child) {
      &::before {
        content: '';
        position: absolute;
        top: calc(100% + 40px);
        right: 0;
        width: calc(400% + 48px);
        height: 1px;
        background: linear-gradient(90deg, #F7F7F7 0%, #DEDEDE 50.96%, #FFFFFF 100%);
        @media (max-width: 1279px) {
          content: none;
        }
      }
    }
    @media (max-width: 1279px) {
      flex: 0 0 calc(50% - 8px);
      &:nth-child(2n):not(:last-child) {
        &::before {
          content: '';
          position: absolute;
          top: calc(100% + 24px);
          right: 0;
          width: calc(200% + 16px);
          height: 1px;
          background: linear-gradient(90deg, #F7F7F7 0%, #DEDEDE 50.96%, #FFFFFF 100%);
        }
      }
    }
    @media (max-width: 767px) {
      flex: 0 0 100%;
      &:nth-child(2n):not(:last-child) {
        &::before {
          content: none;
        }
      }
      &:nth-child(1n):not(:last-child) {
        &::before {
          content: '';
          position: absolute;
          top: calc(100% + 16px);
          right: 0;
          width: 100%;
          height: 1px;
          background: linear-gradient(90deg, #F7F7F7 0%, #DEDEDE 50.96%, #FFFFFF 100%);
        }
      }
    }
    img {
      @include contain;
    }
  }
}