.tariffs {
  padding: 140px 0 25px;
  overflow: hidden;
  @media (max-width: 1279px) {
    padding: 80px 0 65px;
  }
  @media (max-width: 767px) {
    padding: 60px 0 30px;
  }
  &__heading {
    margin-bottom: 45px;
    @media (max-width: 1023px) {
      .heading__title {
        max-width: 460px;
        margin: 0 auto;
      }
    }
    @media (max-width: 767px) {
      margin-bottom: 29px;
      .heading{
        &__suptitle {
          margin-bottom: 19px;
        }
        &__title {
          max-width: 300px;
        }
      }
    }
  }
  &__slider {
    overflow: visible;
    &-wrapper {
      @media (max-width: 767px) {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        gap: $gap;
        transform: none !important;
      }
    }
  }
  &__item {
    width: 429px;
    height: auto;
    display: flex;
    flex-direction: column;
    gap: 23px;
    padding: 23px;
    background-color: $white;
    border: 1px solid $grey-light; 
    border-radius: 16px;
    @include shadow-grey;
    position: relative;
    transition: all $transition;
    @media (min-width: 1280px) {
      &:hover {
        box-shadow: none;
        .button {
          background-color: $green-dark;
          color: $black;
        }
      }
    }
    @media (max-width: 767px) {
      width: 100%;
      padding: 15px;
    }
    &_popular {
      border-color: $green;
      @include shadow-green;
      .tariffs__item-list_check {
        li {
          &:before {
            background-color: $green;
          }
        }
      }
    }
    &_private {
      border-color: $black;
      .tariffs__item-list_check {
        li {
          &:before {
            background-color: $black;
            background-image: url(../img/content/check-white.svg);
          }
        }
      }
    }
    &-label {
      position: absolute;
      top: 0;
      right: 25px;
      padding: 8px 20px;
      background-color: $green;
      border-radius: 0 0 16px 16px;
      @include label;
      @media (max-width: 767px) {
        right: 15px;
      }
    }
    &-title {
      @include h2-bold;
      font-size: 32px;
      letter-spacing: normal;
      margin-bottom: 12px;
      @media (max-width: 1279px) {
        font-size: 32px;
      }
      @media (max-width: 767px) {
        font-size: 32px;
      }
    }
    &-price {
      @include h2-medium;
      font-variant-numeric: lining-nums;
      margin-bottom: 12px;
    }
    &-category {
      @include body-bold;
      color: $grey-dark;
    }
    &-body {
      margin-bottom: 25px;
      @media (max-width: 767px) {
        margin-bottom: -23px;
      }
    }
    &-info {
      padding: 23px 0;
      border-top: 1px solid $grey-light;
    }
    &-subtitle {
      @include body-bold;
      margin-bottom: 12px;
    }
    &-list {
      padding-left: 16px;
      li {
        @include body-medium;
        position: relative;
        &::before {
          content: '';
          position: absolute;
          top: 50%;
          left: -16px;
          transform: translateY(-50%);
          width: 8px;
          height: 8px;
          border-radius: 50%;
          background-color: $grey;
        }
        &:not(:last-child) {
          margin-bottom: 10px;
        }
      }
      &_check {
        padding-left: 24px;
        li {
          &::before {
            left: -24px;
            width: 16px;
            height: 16px;
            background: $grey-light url(../img/content/check.svg) 50% 55% / 10px no-repeat;
          }
        }
      }
    }
    &-bottom {
      margin-top: auto;
    }
    &-small {
      max-width: 256px;
      margin: 0 auto 24px;
      @include body-medium;
      text-align: center;
    }
    &-button {
      width: 100%;
      box-shadow: none;
    }
  }
}