.burger {
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 47px;
  height: 47px;
  flex-shrink: 0;
  padding: 5px;
  background-color: $white;
  border: 1px solid $grey-light;
  border-radius: 10px;
  @include shadow-grey;
  position: relative;
  z-index: 1;
  transition: all $transition;
  @media (max-width: 1279px) {
    display: flex;
  }
  span {
    width: 19px;
    height: 2px;
    background-color: $black;
    transition: all $transition;
    &:not(:last-child) {
      margin-bottom: 4px;
    }
  }
  &_active {
    span {
      margin: 0;
      &:nth-child(1) {
        transform: rotate(45deg);
        margin-bottom: -1px;
      }
      &:nth-child(2) {
        display: none;
      }
      &:nth-child(3) {
        transform: rotate(-45deg);
        margin-top: -1px;
      }
    }
  }
}