.footer {
  background-color: $white;
  &__content {
    padding: 60px 0;
    @media (max-width: 1023px) {
      padding: 80px 0 78px;
    }
    @media (max-width: 767px) {
      padding: 60px 0;
    }
  }
  &__heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: $gap;
    margin-bottom: 38px;
    @media (max-width: 1023px) {
      flex-direction: column;
      justify-content: flex-start;
      align-items: initial;
      gap: 32px;
      margin-bottom: 41px;
    }
  }
  .logo {
    display: inline-flex;
    width: 131px;
    height: 47px;
    flex-shrink: 0;
    svg {
      width: 100%;
      height: 100%;
    }
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 23px;
    @media (max-width: 767px) {
      flex-direction: column;
      align-items: initial;
      gap: 22px;
    }
    a {
      @include body-medium;
    }
  }
  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: $gap;
    @media (max-width: 1023px) {
      display: block;
    }
    &-info {
      display: flex;
      align-items: center;
      gap: 32px;
      @media (max-width: 1023px) {
        gap: $gap;
        margin-bottom: 32px;
      }
      @media (max-width: 767px) {
        flex-direction: column;
        align-items: initial;
        gap: 15px;
      }
    }
    &-link {
      display: flex;
      align-items: center;
      gap: 6px;
      @include label;
      color: $grey-dark;
      @media (max-width: 767px) {
        gap: 5px;
      }
      svg {
        margin-top: -1px;
        width: 37px;
        height: 10px;
      }
    }
  }
}