.connection {
  padding: 15px 0;
  background-color: $green;
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: $gap;
    @media (max-width: 767px) {
      display: block;
    }
  }
  &__title {
    @include h1;
    font-size: 17px;
    @media (max-width: 1279px) {
      font-size: 17px;
    }
    @media (max-width: 767px) {
      font-size: 17px;
      margin-bottom: 32px;
    }
  }
  &__button {
    width: 200px;
  }
}