.button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 46px;
  padding: 0 20px;
  background-color: $green;
  border-radius: 10px;
  @include shadow-green;
  @include button;
  line-height: 1;
  color: $black;
  transition: all $transition;
  @media (max-width: 767px) {
    width: 100%;
  }
  &:hover {
    background-color: $green-dark;
    box-shadow: none;
  }
  &_black {
    background-color: $black;
    color: $white;
    @include shadow-dark;
    &:hover {
      color: $black;
    }
  }
  &_grey {
    background-color: $grey-light;
    @include shadow-dark;
  }
  &_white {
    background-color: $white;
    color: $black;
    box-shadow: none;
    &:hover {
      background-color: $black;
      color: $white;
    }
  }
}