.cookies {
  position: fixed;
  bottom: 30px;
  left: 60px;
  max-width: 461px;
  padding: 16px;
  background-color: #E3E3E3;
  border-radius: 10px;
  z-index: 15;
  @media (max-width: 1365px) {
    bottom: 24px;
    left: 24px;
  }
  @media (max-width: 1279px) {
    bottom: 16px;
    left: 16px;
  }
  @media (max-width: 767px) {
    left: 0;
    right: 0;
    bottom: 0;
    max-width: none;
    border-radius: 0;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: $gap;
    @media (max-width: 767px) {
      flex-direction: column;
      justify-content: flex-start;
      align-items: initial;
    }
  }
  &__description {
    @include label;
  }
  &__button {
    width: 163px;
    flex-shrink: 0;
  }
}