.ticker {
  padding: 8px 0;
  width: 100%;
  overflow: visible;
  @media (max-width: 1279px) {
    padding: 6px;
  }
  &-wrapper {
    transition-timing-function: linear;
    gap: 24px;
    @media (max-width: 1279px) {
      gap: 8px;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    width: auto;
    gap: 24px;
    @media (max-width: 1279px) {
      gap: 8px;
    }
  }
  &__elem {
    width: auto;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    padding: 20px 22px;
    background-color: $white;
    border-radius: 10px;
    @include body-medium;
    color: $grey-dark;
    @media (max-width: 1279px) {
      height: 34px;
      padding: 5px 22px;
      @include label;
      line-height: 1;
      letter-spacing: normal;
    }
  }
}