.feature {
  @include section;
  background-color: $white;
  @media (max-width: 767px) {
    overflow: hidden;
  }
  &__body {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: $gap;
    align-items: flex-start;
    @media (max-width: 1279px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 767px) {
      display: block;
    }
  }
  &__heading {
    grid-column: span 5;
    position: sticky;
    top: 111px;
    min-height: 543px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background: radial-gradient(122.19% 89.58% at 19.02% 87.54%, #f7f7f7 0%, #fff 30.29%, #d5f589 62.98%, #a7e907 100%);
    border-radius: 24px;
    @media (max-width: 1279px) {
      min-height: auto;
      height: 408px;
      grid-column: span 1;
      justify-content: initial;
    }
    @media (max-width: 767px) {
      margin-bottom: 16px;
      position: static;
    }
    &-text {
      padding: 47px 42px 0;
      text-align: center;
      margin-bottom: -81px;
      @media (max-width: 1279px) {
        max-width: 360px;
        padding: 24px 24px 0;
        margin-bottom: -35px;
      }
      @media (max-width: 767px) {
        padding: 25px 24px 0;
        margin-bottom: -18px;
      }
    }
    &-title {
      @include h1;
      margin-bottom: 12px;
      @media (max-width: 767px) {
        margin-bottom: 17px;
      }
    }
    &-subtitle {
      @include h2-medium;
    }
    &-img {
      width: 100%;
      height: auto;
      pointer-events: none;
      @media (max-width: 1279px) {
        min-height: 0;
        width: 95%;
      }
      @media (max-width: 767px) {
        width: 100%;
      }
      img {
        @include contain;
      }
    }
  }
  &__content {
    grid-column: span 7;
    @media (max-width: 1279px) {
      grid-column: span 1;
    }
  }
  &__slider {
    overflow: visible;
    &-wrapper {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: $gap;
      @media (max-width: 1279px) {
        grid-template-columns: repeat(1, 1fr);
      }
      @media (max-width: 767px) {
        display: flex;
        gap: 0;
        margin-bottom: 16px;
      }
    }
  }
  &__item {
    width: 100%;
    height: auto;
    min-height: 543px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 40px 24px 0;
    border-radius: 24px;
    text-align: center;
    position: relative;
    z-index: 1;
    overflow: hidden;
    @media (max-width: 1279px) {
      min-height: auto;
      height: 408px;
      padding: 30px 30px 0;
    }
    @media (max-width: 767px) {
      padding: 30px 14px 0;
    }
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: radial-gradient(66.59% 67.85% at 37.04% 61.79%, #fff 0%, #dadada 100%);
      opacity: 0.35;
      border-radius: inherit;
      z-index: -1;
    }
    &-title {
      @include h3-medium;
      margin-bottom: 21px;
      @media (max-width: 1279px) {
        margin-bottom: 24px;
      }
      @media (max-width: 767px) {
        max-width: 290px;
        margin-bottom: 19px;
      }
      @media (max-width: 374px) {
        min-height: 61px;
      }
    }
    &-img {
      width: 246px;
      height: auto;
      // filter: drop-shadow(3px 4px 18px rgba($black, .2));
      @media (max-width: 1279px) {
        width: 184px;
      }
      img {
        @include contain;
      }
    }
  }
}