.social {
  display: inline-flex;
  gap: $gap;
  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    flex-shrink: 0;
    background-color: $black;
    transition: all $transition;
    @media (min-width: 1280px) {
      &:hover {
        background-color: $green;
        svg {
          fill: $black;
        }
      }
    }
    svg {
      width: 24px;
      height: 24px;
      fill: $white;
      transition: all $transition;
    }
  }
}